.time {
    color: white;
    min-height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    &__clock{
        width: 100vw;
        text-align: center;
        font-size: 3rem;
        border-bottom: 2px solid rgba(0,0,0,0.5);
        font-family: 'Cutive Mono', monospace;
    }
    &__scramble{
        font-size: .75rem;
        color: #a5a5a5;
        margin: 5px 10px 0 10px;
    }
}